.post{
  width: 385px;
  margin: 0px 25px 40px 25px;
}

.postImg{
  width: 100%;
  height: 280px;
  object-fit: cover;
  border-radius: 7px;
}

.postInfo{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.postCat{
  font-size: 12px;
  font-family: 'Varela', sans-serif;
  color: #be9656;
  line-height: 20px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}


.postTitle{
  font-size: 24px;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: bold;
  color: #222;
  margin-top: 15px;
  cursor: pointer;
}

.postDate{
  font-size: 13px;
  font-family: 'Lora',serif;
  font-style: italic;
  color: #999;
  margin-top: 15px;
}

.postDesc{
  font-family: 'Varela', sans-serif;
  font-size: 16px;
  color: #444;
  line-height: 24px;
  margin-top: 15px;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}